p {
    padding: 0;
    margin: 0;
    font-family: "Montserrat Alternates";
    font-weight: 400;
    font-size: 12px;
    line-height: 170%;
}

.stone-section {
    padding: 36px 0 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.stone-title {
    font-family: "Montserrat Alternates";
    font-weight: 400;
    font-size: 16px;

    margin-bottom: 36px;
    text-align: center;
}

.stone-btn {
    background-color: black;
    width: 182px;
    height: 35px;
    border-radius: 20px;
    font-family: "Montserrat Alternates";
    font-weight: 400;
    font-size: 12px;
    color: #fff;
    cursor: pointer;
    margin: 0 auto;
    display: block;
}

.stone-computer,
.stone-you {
    position: relative;
}



.cards-list {
    display: flex;
    margin: 0 auto;
    list-style: none;
    padding: 0;
    justify-content: center;

    &__item {
        padding: 0;
        margin: 0 38px 0 0;

        &:nth-child(3) {
            margin: 0;
        }
    }
}


.cards {
    background-color: black;
    width: 60px;
    height: 60px;
    border-radius: 20px;
    cursor: pointer;

    &:hover {
        -webkit-box-shadow: 0px 0px 10px 7px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0px 0px 10px 7px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 0px 10px 7px rgba(0, 0, 0, 0.2);
    }

    &__img {
        padding: 10px;
    }
}

.cards-container {
    position: relative;
}


.list-score {
    position: absolute;
    right: -40px;
    top: -50px;
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 0;
    margin-right: 300px;

    &__item {
        display: flex;
        gap: 3px;
    }
}

.result {
    font-family: "Montserrat Alternates";
    font-weight: 400;
    font-size: 12px;
    color: #039900;
    margin: 35px;opacity: 0;
    text-align: center;
}

