.dino-game {
  height: 190px;
  width: 617px;
  border: none;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.dino-game__ground {
  height: 14px;
  width: 100%;
  background-image: url("track.81c71a41.png");
  background-repeat: repeat-x;
  background-size: contain;
  position: absolute;
  bottom: 0;
  left: 0;
}

#ground1 {
  left: 0;
}

#ground2 {
  left: 100%;
}

.dino-game__ground.move {
  animation: groundMove 2.5s linear infinite;
}

@keyframes groundMove {
  0% {
    left: 0;
  }

  100% {
    left: -100%;
  }
}

.dino-game__dino {
  width: 54px;
  height: 56px;
  position: absolute;
  bottom: 5px;
  left: 10%;
}

.dino-lose {
  display: none;
}

.dino-game__dino.jump {
  animation: jump .7s;
}

@keyframes jump {
  0% {
    bottom: 0;
  }

  25% {
    bottom: 100px;
  }

  50% {
    bottom: 100px;
  }

  100% {
    bottom: 0;
  }
}

.dino-game__cactus {
  width: 24px;
  height: 46px;
  position: absolute;
  bottom: 3px;
  left: 96%;
}

.dino-game__cactus.move {
  animation: move 2.5s linear infinite;
}

@keyframes move {
  0% {
    left: 617px;
  }

  100% {
    left: -40px;
  }
}

.dino-game__restart {
  width: 40px;
  height: 40px;
  display: none;
  position: absolute;
  bottom: 30%;
  left: 46%;
}

.dino-game__over {
